import { CompanyDetailsService } from './company-details.service';
import { ApiLoaderService } from '@fuse/services/api-loader.service';
import { ActivatedRoute } from '@angular/router';
import { Component, ViewEncapsulation, OnInit, ViewChild, TemplateRef } from '@angular/core';
import { fuseAnimations } from '@fuse/animations';
import { MatTabChangeEvent } from '@angular/material/tabs';
import { MatDialog, MatDialogRef } from '@angular/material';
import { CompanyDetailsDialogComponent } from './company-details-dialog/company-details-dialog.component';
import { ToasterService } from '@fuse/services/toaster-service.service';
import * as moment from 'moment';

@Component({
  selector: 'app-company-details',
  templateUrl: './company-details.component.html',
  styleUrls: ['./company-details.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations   : fuseAnimations
})
export class CompanyDetailsComponent implements OnInit {
  tabIndex: any;
  @ViewChild('dialogContent') dialogContent: TemplateRef<any>;
  dialogRef: any;
  client: any;
  client_id: string;

  constructor( public dialog: MatDialog, private route: ActivatedRoute,
    private apiloader: ApiLoaderService,
    private toast: ToasterService,
    private comService: CompanyDetailsService) { 
            this.tabIndex = 0;
    }    

  ngOnInit(){
      this.route.params.subscribe(params => {
          this.client_id = params['id'];
            this.getClientDetails(this.client_id);
      });
  }

  tabChanged = (tabChangeEvent: MatTabChangeEvent): void => {
      this.tabIndex = tabChangeEvent.index;
  }

  getClientDetails(id){
    this.apiloader.setLoader(true);
    this.comService.getData('clients/' + id).subscribe(response => {
        if (response.data){
            this.apiloader.setLoader(false);
            this.client = response.data;
        }
    }, error => {
        this.apiloader.setLoader(false);
        this.toast.showError(error);
    });
  }

  editDetails()
  {
      this.dialogRef = this.dialog.open(CompanyDetailsDialogComponent, {
          panelClass: 'contact-form-dialog',
          data      : {
              details: this.client
          }
      });

      this.dialogRef.afterClosed()
          .subscribe(response => {
              if ( !response ) { return; }
              else{
                this.updateClient(response);
              }
          });
  }
  
  updateClient(formData){
    console.log(formData);
    this.apiloader.setLoader(true);
    if (formData.is_paid === 'paid'){ formData.is_paid = 1; }
    else{ formData.is_paid = 0; }

    const sdate = moment(formData.start_date).format('YYYY-MM-DD');

    const body = new URLSearchParams();
    body.set('company_name', formData.company_name);
    body.set('email', formData.email);
    body.set('mission', formData.mission);
    body.set('vision', formData.vision);
    body.set('company_values', formData.company_values);
    body.set('description', formData.description);
    body.set('uen', formData.uen);
    body.set('tax_number', formData.tax_number);
    body.set('contact_number', formData.contact_number);
    body.set('website', formData.website);
    body.set('address', formData.address);
    body.set('start_date', sdate);
    body.set('is_paid', formData.is_paid);

    this.comService.postData('clients/' + this.client_id, body.toString()).subscribe(response => {
        if (response.data){
            this.apiloader.setLoader(false);
            this.getClientDetails(this.client_id);
            this.toast.showSuccess('Client Updated Successfully!.');
        }
    }, error => {
        this.apiloader.setLoader(false);
        this.toast.showError(error);
    });
  }

}
