import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { CompanyDetailsComponent } from './company-details.component';
import { RouterModule } from '@angular/router';
import { MatButtonModule, MatRadioModule, MatDividerModule, MatIconModule, MatTabsModule,
               MatCheckboxModule, MatDatepickerModule, MatFormFieldModule, MatInputModule,
                   MatMenuModule, MatRippleModule, MatSidenavModule, MatTableModule, MatToolbarModule } from '@angular/material';
import { FuseSharedModule } from '@fuse/shared.module';
import { CompanyDetailsService } from './company-details.service';
import { DetailsComponent } from './tabs/details/details.component';
import { UserListComponent } from './tabs/user-list/user-list.component';
import { OrgChartComponent } from './tabs/org-chart/org-chart.component';
import { UserService } from './tabs/user-list/user-service.service';
import { UserDialogComponent } from './tabs/user-list/user-dialog/user-dialog.component';
import { CdkTableModule } from '@angular/cdk/table';
import { OrgChartModule } from 'ng2-org-chart';
import { FileUploadModule } from 'ng2-file-upload';
import { CompanyDetailsDialogComponent } from './company-details-dialog/company-details-dialog.component';
import { ImgCropperComponent } from './img-cropper/img-cropper.component';
import { ImageCropperModule } from 'ngx-image-cropper';

const routes = [
  {
      path     : '',
      component: CompanyDetailsComponent,
      resolve  : {
          contacts: UserService
      }
  }
];

@NgModule({
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    MatButtonModule,
    MatDividerModule,
    MatIconModule,
    MatTabsModule,
    MatCheckboxModule,
    MatDatepickerModule,
    MatFormFieldModule,
    MatInputModule,
    MatMenuModule,
    MatRippleModule,
    MatSidenavModule,
    MatTableModule,
    MatToolbarModule,
    MatRadioModule,
    FuseSharedModule,
    CdkTableModule,
    OrgChartModule,
    FileUploadModule,
    ImageCropperModule
  ],
  declarations: [CompanyDetailsComponent, DetailsComponent, UserListComponent, OrgChartComponent, UserDialogComponent, CompanyDetailsDialogComponent, ImgCropperComponent],
  providers: [CompanyDetailsService, UserService ],
  entryComponents: [UserDialogComponent, CompanyDetailsDialogComponent, ImgCropperComponent]
})
export class CompanyDetailsModule { }
