import { ApiLoaderService } from '@fuse/services/api-loader.service';
import { Component, OnDestroy, OnInit, TemplateRef, ViewChild, ViewEncapsulation } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { MatDialog, MatDialogRef } from '@angular/material';
import { DataSource } from '@angular/cdk/collections';
import { Observable, Subscription } from 'rxjs';
import { fuseAnimations } from '@fuse/animations';
import { FuseConfirmDialogComponent } from '@fuse/components/confirm-dialog/confirm-dialog.component';
import {Router, ActivatedRoute} from "@angular/router";
import { UserService } from './user-service.service';
import { UserDialogComponent } from './user-dialog/user-dialog.component';
import { CompanyDetailsService } from '../../company-details.service';
import { ToasterService } from '@fuse/services/toaster-service.service';

@Component({
  selector: 'user-list',
  templateUrl: './user-list.component.html',
  styleUrls: ['./user-list.component.scss'],
  encapsulation: ViewEncapsulation.None,
  animations   : fuseAnimations
})
export class UserListComponent implements OnInit, OnDestroy
{
  @ViewChild('dialogContent') dialogContent: TemplateRef<any>;

  contacts: any;
  user: any;
  dataSource: FilesDataSource | null;
  displayedColumns = [ 'avatar', 'name', 'email', 'phone', 'status'];
  selectedContacts: any[];
  checkboxes: {};

  onContactsChangedSubscription: Subscription;
  onFilterChangedSubscription: Subscription;
  onSelectedContactsChangedSubscription: Subscription;
  onUserDataChangedSubscription: Subscription;

  dialogRef: any;

  confirmDialogRef: MatDialogRef<FuseConfirmDialogComponent>;

  constructor(
      private userservice: UserService,
      public dialog: MatDialog,
      private router: Router,
      private route: ActivatedRoute,
      private apiloader: ApiLoaderService,
      private toast: ToasterService,
      private comService: CompanyDetailsService
  )
  {
      this.onContactsChangedSubscription =
          this.userservice.onContactsChanged.subscribe(contacts => {

              this.contacts = contacts;

              this.checkboxes = {};
              contacts.map(contact => {
                  this.checkboxes[contact.id] = false;
              });
          });

      this.onSelectedContactsChangedSubscription =
          this.userservice.onSelectedContactsChanged.subscribe(selectedContacts => {
              for ( const id in this.checkboxes )
              {
                  if ( !this.checkboxes.hasOwnProperty(id) )
                  {
                      continue;
                  }

                  this.checkboxes[id] = selectedContacts.includes(id);
              }
              this.selectedContacts = selectedContacts;
          });

      this.onUserDataChangedSubscription =
          this.userservice.onUserDataChanged.subscribe(user => {
              this.user = user;
          });

      this.onFilterChangedSubscription =
          this.userservice.onFilterChanged.subscribe(() => {
              this.userservice.deselectContacts();
          });
  }

  ngOnInit()
  {
      this.dataSource = new FilesDataSource(this.userservice);
        this.route.params.subscribe(params => {
            const id = params['id'];
            this.getUsers(id);
        });
  }

  getUsers(id){
    this.apiloader.setLoader(true);
    this.comService.getData('users', {'client_id': id }).subscribe(response => {
        if (response.data){
            this.apiloader.setLoader(false);
            this.dataSource = response.data;
        }
    }, error => {
        this.apiloader.setLoader(false);
        this.toast.showError(error);
    });
  }

  ngOnDestroy()
  {
      this.onContactsChangedSubscription.unsubscribe();
      this.onFilterChangedSubscription.unsubscribe();
      this.onSelectedContactsChangedSubscription.unsubscribe();
      this.onUserDataChangedSubscription.unsubscribe();
  }

  openModal(contact)
  {
      this.dialogRef = this.dialog.open(UserDialogComponent, {
          panelClass: 'contact-form-dialog',
          data      : {
              contact: contact
          }
      });

      this.dialogRef.afterClosed()
          .subscribe(response => {
              if ( !response ){ return; }
          });
  }

  /**
   * Delete Contact
   */
  deleteContact(contact)
  {
      this.confirmDialogRef = this.dialog.open(FuseConfirmDialogComponent, {
          disableClose: false
      });

      this.confirmDialogRef.componentInstance.confirmMessage = 'Are you sure you want to delete?';

      this.confirmDialogRef.afterClosed().subscribe(result => {
          if ( result )
          {
              this.userservice.deleteContact(contact);
          }
          this.confirmDialogRef = null;
      });

  }

  onSelectedChange(contactId)
  {
      this.userservice.toggleSelectedContact(contactId);
  }

  toggleStar(contactId)
  {
      if ( this.user.starred.includes(contactId) )
      {
          this.user.starred.splice(this.user.starred.indexOf(contactId), 1);
      }
      else
      {
          this.user.starred.push(contactId);
      }

      this.userservice.updateUserData(this.user);
  }

}

export class FilesDataSource extends DataSource<any>
{
    constructor(private userservice: UserService)
    {
        super();
    }

    /** Connect function called by the table to retrieve one stream containing the data to render. */
    connect(): Observable<any[]>
    {
        return this.userservice.onContactsChanged;
    }

    disconnect()
    {
    }
}
