import { Component, ViewEncapsulation, Inject, EventEmitter } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef, MatDialog } from '@angular/material';
import { FileUploader, FileItem } from 'ng2-file-upload';
import { ImgCropperComponent } from '../img-cropper/img-cropper.component';

@Component({
  selector: 'app-company-details-dialog',
  templateUrl: './company-details-dialog.component.html',
  styleUrls: ['./company-details-dialog.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class CompanyDetailsDialogComponent{
    public uploader: FileUploader;
    imageChangedEvent: any = '';
    croppedImage: any = '';
    cropDialog: any;
    myform: FormGroup;
    client_details: any;
    constructor(
      public dialogRef: MatDialogRef<CompanyDetailsDialogComponent>,
      @Inject(MAT_DIALOG_DATA) private data: any,
      private formBuilder: FormBuilder, public dialog: MatDialog) {
  
        this.uploader = new FileUploader({
            url: 'http://localhost/img/upload.php',
            disableMultipart: false,
            autoUpload: false
        });
          this.client_details = data.details;
          console.log(data);
          this.myform = this.createForm();
          this.myform.patchValue({'company_name': this.client_details.name, 'start_date': this.client_details.start_date, 'uen': this.client_details.uen,
                                'tax_number' : this.client_details.tax_number, 'address' : this.client_details.address, 'mobile_no' : this.client_details.contact_number,
                                'website' : this.client_details.website, 'email' : this.client_details.email, 'description':  this.client_details.description, 
                                'vision': this.client_details.vision, 'mission': this.client_details.mission, 'company_values': this.client_details.company_values,
                                'contact_number': this.client_details.contact_number});
          
          if (this.client_details.is_paid ===  1 ){ this.myform.patchValue({ 'is_paid': 'paid' }); }
          else{ this.myform.patchValue({ 'is_paid': 'free' }); }
      }
  
    createForm() {
  
      return this.formBuilder.group({
        logo: [''],
        company_name : ['', Validators.required],
        description : [''],
        vision : [''],
        mission : [''],
        company_values : [''],
        start_date : [''],
        uen: [''],
        tax_number: [''],
        address : ['', Validators.required],
        contact_number  : [''],
        website  : [''],
        is_paid: [''],
        email : ['', Validators.required]
    });
    
    }
  
      fileChangeEvent(event: any): void {
          this.imageChangedEvent = event;
          this.cropDialog = this.dialog.open(ImgCropperComponent, {
              panelClass: 'image-cropper',
              data      : {
                  action: event,
                  height: '400px'
              }
          });
  
          this.cropDialog.afterClosed()
              .subscribe(response => {
                  if ( !response )
                  {
                      return '';
                  }else{
                      this.croppedImage = response;
                  }
              });
      }
  
      public onFileSelected(event: EventEmitter<File[]>) {
          const file: File = event[0];
      }

}
